import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import { withRouter } from 'react-router-dom';

const useStyles = makeStyles(theme => ({
  sidebarAboutBox: {
    padding: theme.spacing(2),
    backgroundColor: theme.palette.grey[200],
  },
  sidebarSection: {
    marginTop: theme.spacing(3),
  },
}));

const Sidebar = props => {
  const classes = useStyles();
  const { archives, title } = props;

  // YouTube video ID
  const videoID = 'pEcCJlsCD9M';

  // Fetch video info (using a proxy if avoid CORS errors)
  // eslint-disable-next-line no-useless-concat
  fetch(
    'https://cors-anywhere.herokuapp.com/' +
      'https://www.youtube.com/get_video_info?video_id=' +
      videoID
  ).then(response => {
    if (response.ok) {
      response.text().then(ytubeData => {
        // parse response to find audio info
        const ytData = parse_str(ytubeData);
        const getAdaptiveFormats = JSON.parse(ytData.player_response)
          .streamingData.adaptiveFormats;
        const findAudioInfo = getAdaptiveFormats.findIndex(
          obj => obj.audioQuality
        );

        // get the URL for the audio file
        const audioURL = getAdaptiveFormats[findAudioInfo].url;

        // update the <audio> element src
        const youtubeAudio = document.getElementById('youtube');
        youtubeAudio.src = audioURL;
      });
    }
  });

  function parse_str(str) {
    return str.split('&').reduce(function (params, param) {
      const paramSplit = param.split('=').map(function (value) {
        return decodeURIComponent(value.replace('+', ' '));
      });
      params[paramSplit[0]] = paramSplit[1];
      return params;
    }, {});
  }

  return (
    <Grid item xs={12} md={4}>
      <Paper elevation={0} className={classes.sidebarAboutBox}>
        <Typography variant='h6' gutterBottom>
          {title}
        </Typography>
        <div class='powr-hit-counter' id='e9e76d77_1604878551'></div>
        <div align='center'>
          <audio id='youtube' controls></audio>
        </div>
      </Paper>
      <Typography variant='h6' gutterBottom className={classes.sidebarSection}>
        Otros Enlaces
      </Typography>
      {archives.map(archive => (
        <div style={{ margin: '10px 0' }}>
          <a
            style={{ color: 'rgb(33 33 33)' }}
            href={archive.url}
            target='_blank'
            rel='noreferrer'
            key={archive.title}
          >
            {archive.title}
          </a>
        </div>
      ))}
    </Grid>
  );
};

Sidebar.propTypes = {
  archives: PropTypes.array,
  description: PropTypes.string,
  title: PropTypes.string,
};

export default withRouter(Sidebar);
