import React from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import Header from './Header';
import Footer from './Footer';
import HomePage from '../views/home/Home';
import { Route, Switch } from 'react-router-dom';
import { MetodoBiseccion } from '../assets/posts/MetodoBiseccion.markdown';
import { MetodoEuler } from '../assets/posts/MetodoEuler.markdown';
import { MetodoNewtonRaphson } from '../assets/posts/MetodoNewtonRaphson.markdown';
import { MetodoPuntoFijo } from '../assets/posts/MetodoPuntoFijo.markdown';
import MethodsPage from '../views/methods/Methods';

const sections = [
  { title: 'Metodo de Biseccion', url: '/calc-blog/metodo-de-biseccion' },
  { title: 'Metodo de Punto Fijo', url: '/calc-blog/metodo-de-punto-fijo' },
  { title: 'Metodo de Newton-Raphson', url: '/calc-blog/metodo-de-newton-raphson' },
  { title: 'Metodo de Euler', url: '/calc-blog/metodo-de-euler' },
];

export default function Blog() {

  return (
    <React.Fragment>
      <CssBaseline />
      <Container maxWidth='lg'>
        <Header title='Blog' sections={sections} />
      </Container>
      <Switch>
        <Route exact path='/calc-blog' component={HomePage} />
        <Route exact path={`/calc-blog/metodo-de-biseccion`} render={() => <MethodsPage posts={[MetodoBiseccion]}/>}/>
        <Route exact path={`/calc-blog/metodo-de-punto-fijo`} render={() => <MethodsPage posts={[MetodoPuntoFijo]}/>}/>
        <Route exact path={`/calc-blog/metodo-de-euler`} render={() => <MethodsPage posts={[MetodoEuler]}/>}/>
        <Route exact path={`/calc-blog/metodo-de-newton-raphson`} render={() => <MethodsPage posts={[MetodoNewtonRaphson]}/>}/>
      </Switch>
      <Footer
        title='Fred Zerpa'
        description='Un blog sobre Calculo Numerico.'
      />
    </React.Fragment>
  );
}
