import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Main from '../../blog/Main';
import Sidebar from '../../blog/Sidebar';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';

const sidebar = {
  title: 'Informacion',
  archives: [
    {
      title: 'Metodo de Biseccion',
      url: 'https://matematica.laguia2000.com/general/metodo-de-biseccion',
    },
    {
      title: 'Metodo de Punto Fijo',
      url:
        'http://blog.espol.edu.ec/analisisnumerico/2-4-1-punto-fijo-ejemplo01/',
    },
    {
      title: 'Metodo de Newton-Raphson',
      url: 'https://www.geogebra.org/m/XCrwWHzy',
    },
    {
      title: 'Metodo de Euler',
      url: 'https://www.lifeder.com/metodo-de-euler/',
    },
  ],
};

const useStyles = makeStyles(theme => ({
  mainGrid: {
    marginTop: theme.spacing(3),
  },
}));

const MethodsPage = (props) => {
  const classes = useStyles();
  const {posts} = props;
  return (
    <Container maxWidth='lg'>
      <main>
        <Grid container spacing={5} className={classes.mainGrid}>
          <Main posts={posts} />
          <Sidebar
            title={sidebar.title}
            description={sidebar.description}
            archives={sidebar.archives}
          />
        </Grid>
      </main>
    </Container>
  );
};

export default MethodsPage;
