import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import MainFeaturedPost from '../../blog/MainFeaturedPost';
import FeaturedPost from '../../blog/FeaturedPost';
import Main from '../../blog/Main';
import Sidebar from '../../blog/Sidebar';
import Grid from '@material-ui/core/Grid';
import Container from '@material-ui/core/Container';
import {HomePost} from '../../assets/posts/HomePost.markdown';

const mainFeaturedPost = {
  title: 'Metodo de Biseccion',
  description:
    "El método de la bisección, conocido también como de corte binario, de partición en dos intervalos iguales, de búsqueda binaria o de Bolzano se basa..",
  image: 'https://images.unsplash.com/photo-1542621334-a254cf47733d?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1350&q=80',
  imgText: 'main image description',
  linkText: 'Continue reading…',
};

const featuredPosts = [
  {
    title: 'Metodo de Euler',
    // date: 'Nov 12',
    description:
      'Una de las técnicas más simples para aproximar soluciones de una ecuación diferencial es el método de Euler.',
    image: 'https://upload.wikimedia.org/wikipedia/commons/thumb/1/10/Euler_method.svg/1200px-Euler_method.svg.png',
    imageText: 'Image Text',
    url: '/calc-blog/metodo-de-euler'
  },
  {
    title: 'Metodo de Punto Fijo',
    // date: 'Nov 11',
    description:
    'El método del punto fijo es un método iterativo que permite resolver sistemas de ecuaciones no necesariamente lineales..',
    image: 'https://upload.wikimedia.org/wikipedia/commons/thumb/8/8b/Sine_fixed_point.svg/250px-Sine_fixed_point.svg.png',
    imageText: 'Image Text',
    url: '/calc-blog/metodo-de-punto-fijo'
  },
];

const posts = [HomePost];

const sidebar = {
  title: 'Informacion',
  archives: [
    { title: 'Metodo de Biseccion', url: 'https://matematica.laguia2000.com/general/metodo-de-biseccion' },
    { title: 'Metodo de Punto Fijo', url: 'http://blog.espol.edu.ec/analisisnumerico/2-4-1-punto-fijo-ejemplo01/' },
    { title: 'Metodo de Newton-Raphson', url: 'https://www.geogebra.org/m/XCrwWHzy' },
    { title: 'Metodo de Euler', url: 'https://www.lifeder.com/metodo-de-euler/' },
  ],
};

const useStyles = makeStyles(theme => ({
  mainGrid: {
    marginTop: theme.spacing(3),
  },
}));

const HomePage = () => {
  const classes = useStyles();
  return (
    <Container maxWidth='lg'>
      <main>
        <MainFeaturedPost post={mainFeaturedPost} />
        <Grid container spacing={4}>
          {featuredPosts.map(post => (
            <FeaturedPost key={post.title} post={post} />
          ))}
        </Grid>
        <Grid container spacing={5} className={classes.mainGrid}>
          <Main title='Calculo Numerico y sus Metodos' posts={posts} />
          <Sidebar
            title={sidebar.title}
            description={sidebar.description}
            archives={sidebar.archives}
          />
        </Grid>
      </main>
    </Container>
  );
}

export default HomePage;